nav {
	position: absolute;
	width: 100%;
	height: 100%;
	color: var(--white);
	inset: 0;
	margin: auto;
}

nav a {
	cursor: pointer;
}

nav .wave-up,
nav .wave-down {
	position: absolute;
	width: calc(clamp(4px, 2vw, 30px));
	height: calc(clamp(4px, 2vw, 30px));
	left: 50%;
	transform: translateX(-50%);
	margin: 1% 0;
	border-radius: 50%;
}

nav .wave-up .to-up,
nav .wave-up .to-down,
nav .wave-down .to-up,
nav .wave-down .to-down {
	position: absolute;
	display: block;
	left: 50%;
	transform: translateX(-50%);
	width: calc(clamp(4px, 2vw, 30px));
	height: calc(clamp(4px, 2vw, 30px));
	border-radius: 50%;
	cursor: pointer;
}

@-webkit-keyframes wave {
	0% {
		transform: translate(-50%, -50%) scale(0);
	}
	50% {
		transform: translate(-50%, -50%) scale(1.2);
		opacity: 1;
	}
	100% {
		transform: translate(-50%, -50%) scale(1.5);
		opacity: 0;
	}
}

@keyframes wave {
	0% {
		transform: translate(-50%, -50%) scale(0);
	}
	50% {
		transform: translate(-50%, -50%) scale(1.2);
		opacity: 1;
	}
	100% {
		transform: translate(-50%, -50%) scale(1.5);
		opacity: 0;
	}
}

nav .wave-up::before,
nav .wave-down::before {
	content: "";
	width: 100%;
	height: 100%;
	transform: translate(-50%, -50%) scale(0);
	border: 1px solid var(--background-color3);
	border-radius: 50%;
	opacity: 0;
}

nav .wave-up .to-up::before,
nav .wave-up .to-up::after,
nav .wave-down .to-down::before,
nav .wave-down .to-down::after {
	content: "";
	position: absolute;
	display: block;
	inset: 0;
	margin: auto;
	width: 50%;
	height: 1%;
	background-color: var(--light-color);
}

nav .wave-up {
	top: 0;
}

nav .wave-up:hover::before {
	-webkit-animation: wave 1.2s linear infinite;
	animation: wave 1.2s linear infinite;
}

nav .wave-up .to-up::before {
	transform: translateY(0) translateX(38%) rotate(45deg);
}

nav .wave-up .to-up::after {
	transform: translateY(0) translateX(-38%) rotate(-45deg);
}

nav .wave-up .to-up:hover::before {
	-webkit-animation: to-up-before 1.2s linear infinite;
	animation: to-up-before 1.2s linear infinite;
}

nav .wave-up .to-up:hover::after {
	-webkit-animation: to-up-after 1.2s linear infinite;
	animation: to-up-after 1.2s linear infinite;
}

nav .wave-up .to-up:hover::before,
nav .wave-up .to-up:hover::after {
	-webkit-animation-delay: 0.6s;
	animation-delay: 0.6s;
}

@-webkit-keyframes to-up-before {
	0% {
		transform: translateY(0) translateX(38%) rotate(45deg);
	}
	50% {
		transform: translateY(-300%) translateX(38%) rotate(45deg);
	}
	51% {
		transform: translateY(300%) translateX(38%) rotate(45deg);
	}
	100% {
		transform: translateY(0) translateX(38%) rotate(45deg);
	}
}

@keyframes to-up-before {
	0% {
		transform: translateY(0) translateX(38%) rotate(45deg);
	}
	50% {
