.top-side {
	z-index: 110;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	left: 90%;
	height: 7%;
	display: inline-flex;
	align-items: center;
	justify-content: flex-end;
}

.top-side li {
	color: var(--word-color);
	font-size: 0.8rem;
	margin-right: 3rem;
}

.top-side li .page {
	position: relative;
	transition: 0.5s;
	cursor: pointer;
}

.top-side li .page.active::before {
	position: absolute;
	content: "";
	height: 1%;
	width: 100%;
	background-color: var(--primary-color1);
	bottom: -15%;
}

.top-side li .page::after {
	position: absolute;
	left: 50%;
	top: 50%;
	content: "";
	height: 0.8%;
	width: 100%;
	padding: 0 20%;
	background-color: var(--secondary-color1);
	transition: 0.5s;
	transform: translate(-50%, -50%) scaleX(0);
}

.top-side li .page .link-width {
	opacity: 0;
}

.top-side li .page span:not(.link-width) {
	transition: 0.5s;
}

.top-side li .page span:not(.link-width):nth-child(2) {
	background-image: linear-gradient(to bottom, var(--word-color) 51%, transparent 49%, transparent);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.top-side li .page span:not(.link-width):nth-child(3) {
	background-image: linear-gradient(to top, var(--word-color) 51%, transparent 49%);
	-webkit-background-clip: text;
	background-size: 100%;
	-webkit-text-fill-color: transparent;
}

.top-side li .page:hover span:not(.link-width) {
	transition-delay: 0.3s;
}

.top-side li .page:hover span:not(.link-width):nth-child(2) {
	left: 40%;
}

.top-side li .page:hover span:not(.link-width):nth-child(3) {
	left: 60%;
}

.top-side li .page:hover::after {
	transform: translate(-50%, -50%) scaleX(1);
}

