.home {
	position: absolute;
	width: 90%;
	height: 80%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 10;
}

.home .main {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.home .main h1 {
	position: relative;
	font-size: 6rem;
	margin-bottom: 3%;
	color: var(--secondary-color1);
	letter-spacing: 0.5rem;
	opacity: 0;
	-webkit-animation: name-effect 1s forwards;
	animation: name-effect 1s forwards;
	-webkit-animation-delay: var(--animation-delay);
	animation-delay: var(--animation-delay);
}

@-webkit-keyframes name-effect {
	0% {
		transform: translateY(50%);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}

@keyframes name-effect {
	0% {
		transform: translateY(50%);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}

.home .main h2 {
	position: relative;
	font-size: 3rem;
	color: var(--word-color);
	letter-spacing: 0.1rem;
	opacity: 0;
	-webkit-animation: name-effect 1s forwards;
	animation: name-effect 1s forwards;
	-webkit-animation-delay: calc(var(--animation-delay) * 1.3);
	animation-delay: calc(var(--animation-delay) * 1.3);
}

.home .main .buttonContainer {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
}

.home .main .button {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 12%;
	width: 30%;
	padding: 2.5% 0;
	color: var(--word-color);
	border-radius: 50px;
	font-size: 1.5rem;
	font-weight: bold;
	cursor: pointer;
	overflow: hidden;
	opacity: 0;
	-webkit-animation: name-effect 1s forwards;
	animation: name-effect 1s forwards;
	-webkit-animation-delay: calc(var(--animation-delay) * 1.6);
	animation-delay: calc(var(--animation-delay) * 1.6);
	transition: 0.1s;
}

.home .main .button .right-arrow {
	content: "";
	position: absolute;
	width: 27%;
	height: 100%;
	background-color: var(--primary-color1);
	border-radius: 100px;
	left: 50%;
	transform: translateX(50%);
	transition: transform 0.5s cubic-bezier(0.075, 0.82, 0.165, 1), width 0.5s cubic-bezier(0, 1.64, 0.27, 0.82), left 0.4s;
}

.home .main .button span {
	transition: 1.2s;
	color: var(--word-color);
	transform: translateY(0) scale(1);
}

.home .main .button:hover {
	transition: 0.5s;
	transition-delay: 0.5s;
	box-shadow: 0 5px 5px var(--background-color1);
}

.home .main .button:hover span {
	position: relative;
	-webkit-animation: about 1.2s forwards;
	animation: about 1.2s forwards;
}

.home .main .button:hover .right-arrow {
	transition: background-color 0.5s, transform 0.3s cubic-bezier(0.075, 0.82, 0.165, 1), width 0.6s cubic-bezier(0.45, -0.02, 0.82, 0.73), left 0.3s;
	transform: translateX(-50%);
	background-color: var(--secondary-color1);
	width: 250%;
	left: 0;
}

@-webkit-keyframes about {
	0% {
	}
	25% {
		transform: translateY(-200%);
	}
	26% {
		transform: translateY(200%);
		opacity: 0;
	}
	55%,
	60% {
		transform: translateY(0);
		color: var(--background-color1);
	}
	80% {
		transform: translateY(0) scale(1.2);
	}
	81% {
		color: var(--background-color1);
	}
	100% {
		transform: translateY(0) scale(1);
		color: var(--background-color1);
	}
}

@keyframes about {
