:root {
	--background-color1: black;
	--background-color2: #1a1a1a;
	--background-color3: #303030;
	--light-color: rgb(161, 158, 158);
	--word-color: white;
	--primary-color1: #5d3cde;
	--secondary-color1: #acaaff;
	--accent-color1: #c479ff;
	--animation-delay: 0.7s;
}

#root {
	position: absolute;
	width: 100%;
	height: 100%;
	inset: 0;
	margin: auto;
}

* {
	margin: 0;
	padding: 0;
	list-style: none;
	font-family: "Inter", sans-serif;
}

.br::before,
.br-work::before {
	content: "\A";
	white-space: pre;
}

.coron::after {
	content: ":";
}

html {
	font-size: clamp(6px, 0.8vw, 16px);
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100vw;
	height: 100vh;
	background-color: var(--background-color1);
}

body {
	position: relative;
	width: 100%;
	max-width: 1920px;
	padding-bottom: calc(min(56.25%, 1080px));
	background-color: var(--background-color2);
	border-radius: 10px;
	overflow: hidden;
}

.content {
	-webkit-animation: content-appear 1s forwards;
	animation: content-appear 1s forwards;
	-webkit-animation-delay: calc(var(--animation-delay) - 0.5s);
	animation-delay: calc(var(--animation-delay) - 0.5s);
	opacity: 0;
}

@-webkit-keyframes content-appear {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes content-appear {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.sColor {
	color: var(--primary-color1);
	background: linear-gradient(to right top, var(--primary-color1), var(--secondary-color1));
	-webkit-background-clip: text;
	background-clip: text;
	-webkit-text-fill-color: transparent;
	text-fill-color: transparent;
}

.line::before,
.line::after {
	position: absolute;
	content: "";
	height: 35%;
	width: 0.2%;
	background-color: var(--primary-color1);
}

.title {
	position: absolute;
	left: 5%;
	top: 50%;
	transform: translateY(50%) rotate(-90deg);
	transform-origin: left;
	color: var(--light-color);
	font-size: 6rem;
	letter-spacing: 0.5rem;
	font-family: "Inria Serif", serif;
	font-weight: 300;
}

.line {
	z-index: -1;
	position: absolute;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: space-between;
}

.line::before {
	left: 5%;
	top: -12.5%;
}

.line::after {
	right: 5%;
	bottom: -12.5%;
}

.secondary-color {
	color: var(--secondary-color1);
	-webkit-filter: drop-shadow(1px 1px 5px black);
	filter: drop-shadow(1px 1px 5px black);
}

p,
h1,
h2 {
	-webkit-filter: drop-shadow(1px 1px 5px black);
	filter: drop-shadow(1px 1px 5px black);
}

@media (max-width: 850px) {
	.coron::after {
		content: "";
	}
	.background {
		height: 100vh;
	}
	html {
		font-size: clamp(8px, 1.2vw, 16px);
		background-color: var(--background-color2);
	}
	body {
		position: relative;
		height: 100%;
		padding-bottom: 0;
		border-radius: 0;
		overflow-x: hidden;
		overflow-y: auto;
	}
	.line {
		display: none;
	}
	.title {
		display: block;
		position: relative;
		width: 100%;
		text-align: center;
		top: 0;
		left: 0;
		transform: translateY(0) rotate(0deg);
		margin: 0 0 10% 0;
		padding: 1% 0;
	}
	.title::before,
	.title::after {
		position: absolute;
		content: "";
		width: 80%;
		height: 3px;
		background-color: var(--primary-color1);
	}
	.title::before {
		top: 0;
		left: 0;
	}
	.title::after {
		bottom: 0;
		right: 0;
	}
	.content {
		position: relative;
		flex-direction: column;
		width: 100%;
		height: auto;
		justify-content: flex-start;
		top: 0;
		left: 0;
		transform: translate(0, 0);
	}
	.content .main {
		position: relative;
		top: 0;
		padding-top: 10%;
		width: 90%;
		left: 0;
		transform: translate(0, 0);
	}
}

@media (max-width: 500px) {
	.br::before {
		content: "";
		white-space: auto;
	}
}

@media (max-width: 385px) {
	.br-work::before {
		content: "";
		white-space: auto;
	}
}
